import React, { Component } from 'react'

export default class AboutAction extends Component {
	render() {
		return (
			<div className='main-menu__item main-menu__item_about'>
				<div className='main-menu__item-name'>About</div>
				<div className='main-menu__item-image'></div>
			</div>
		)
	}
}


